@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
}
.input-range {
    width: 100%;
}

.input-range__title-indicator {
    font-size: 1.125em;
    color: white;
    margin-bottom: 10px;
    display: block;
}

.input-range__input {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    margin: 0 -10px;
}

.input-range__limit {
    margin: 0 10px;
    color: white;
    font-size: 1em;
}

.input-range__slider {
    width: 100%;
    -moz-appearance: none;
         appearance: none;
    -webkit-appearance: none;
    border-radius: 50px;
    height: 20px;
    background: white;
    padding: 0 2px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.35) inset;
}

.input-range__slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 18px;
    height: 18px;
    border-radius: 50px;
    background: #10B3A2;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.27);
}

.input-range__slider::-moz-range-thumb {
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
    border-radius: 50px;
    background: #10B3A2;
    border: none;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.27);
}
.checkbox {
    width: 100%;
}

.checkbox--horizontal {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

.checkbox__title {
    font-size: 1.125em;
    color: white;
    margin-bottom: 10px;
    display: block;
    font-weight: 500;
}

.checkbox--horizontal .checkbox__title {
    margin-bottom: 0;
}

.checkbox__container {
    width: 66px;
    height: 38px;
    background: white;
    border-radius: 50px;
    padding: 4px;
    position: relative;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.35) inset;
}

.checkbox__toggle {
    width: 30px;
    height: 30px;
    background: #757575;
    border-radius: 50px;
    position: absolute;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    left: 4px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.27);
}

.checkbox__input {
    display: none;
}

.checkbox__input:checked + .checkbox__container .checkbox__toggle {
    left: 32px;
    background: #10B3A2;
}
.button {
    padding: 10px;
    border: none;
    background: white;
    color: #0f111c;
    font-size: 1.125em;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
}

.button--primary {
    background: #BAFFF8;
}

.button--border-primary {
    box-shadow: 0 0 0 2px #BAFFF8;
}

.button--copy {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
}

.button--big {
    padding: 12px 30px;
}

.button--generate {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.33);
    margin: 0 auto;
    display: block;
}
.password-generator {
    max-width: 500px;
    min-width: 300px;
    width: calc(100% - 20px);
    margin: 70px auto;
    background: #0EB575;
    padding: 15px;
    border-radius: 5px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.36);
}

.password-generator > * {
    margin-bottom: 30px;
}

.password-generator__title {
    color: white;
    text-align: center;
    font-size: 2em;
    margin-bottom: 20px;
}

.password-generator__password-group {
    display: flex;
    flex-wrap: nowrap;
    border: 2px solid white;
    border-radius: 3px;
    min-height: 50px;
    align-items: stretch;
    justify-content: space-between;
}

.password-generator__password {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    color: white;
    word-break: break-all;
}
